.backdrop {
    display: block;
    width: 100%;
}

.siteStatsContainer {
    width: 100%;
    font-size: 12px;
    padding: 0 20px 10px 20px;
    font-weight: 600;
    height: max-content;
    border-radius: 15px;
    background-color: #06083c81;
    backdrop-filter: blur(5px);
    box-shadow: inset 0 0 10px 0 rgba(255, 255, 255, 0.35);
}

.registeredUsers, .totalPeeps, .totalComments {
    font-size: 12px;
    font-weight: 600;
    color: aliceblue;
}

.siteStatsTitle {
    text-align: center;
    font-size: 15px;
    font-weight: 800;
    color: aliceblue;
}

@media screen and (max-width: 700px) {
    .backdrop {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: grid;
        backdrop-filter: blur(10px);
        background-color: rgba(255, 255, 255, 0.05);
    }

    .siteStatsContainer {
        align-self: center;
        justify-self: center;
    }
}